    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                showBox:false,
                aboutInfo: '',
                isBusiness: false
            }
        },
        onShow() {
            this.checkBusiness()
        },
        computed: {
            ...mapState(['userInfo'])
        },
        methods: {
            intoOrder () {
                uni.navigateTo({
                    url: '/pages/orderList/orderList',
                });
            },
            intoMyCard () {                		
				uni.switchTab({
					url: '/pages/myCard/myCard'
				})
            },
            intoRecodeList () {                		
				uni.navigateTo({
					url: '/pages/recodeList/recodeList'
				})
            },
            async getAbout () {
                let res = await this.$api.getAboutInfo()
                if (res.code === 0) {
                    this.aboutInfo = res.data.content
                }                
            },
            intoBusiness () {
                this.$store.commit('SET_ROLE_TYPE','business')
                uni.reLaunch({
                    url: '/pages/business/business'
                });
            },
            async checkBusiness () {                
                let res = await this.$api.checkBusiness().then((res) => {
                    if (res.code === 0) {
                        if (res.data.length > 0) {
                            this.isBusiness = true
                        }
                    }
                })
            },
            gotoExchangePage () {                
                uni.navigateTo({
                    url: `/pages/exchangePage/exchangePage`
                })
            },
            showAbout () {
                this.getAbout()
                this.showBox = true
            },
            closeBox () {
                this.showBox = false
            }
        }
    }
